@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url('../fonts/Roboto-400.woff2') format('woff2'), url('../fonts/Roboto-400.woff') format('woff');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src: url('../fonts/Roboto-Italic-400.woff2') format('woff2'), url('../fonts/Roboto-Italic-400.woff') format('woff');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url('../fonts/Roboto-300.woff2') format('woff2'), url('../fonts/Roboto-300.woff') format('woff');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  font-style: italic;
  font-display: swap;
  src: url('../fonts/Roboto-Italic-300.woff2') format('woff2'), url('../fonts/Roboto-Italic-300.woff') format('woff');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url('../fonts/Roboto-700.woff2') format('woff2'), url('../fonts/Roboto-700.woff') format('woff');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  font-style: italic;
  font-display: swap;
  src: url('../fonts/Roboto-Italic-700.woff2') format('woff2'), url('../fonts/Roboto-Italic-700.woff') format('woff');
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  outline: none;
}
@media (min-width: 768px) {
  .visually-hidden-mobile {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    outline: none;
  }
}
@media (max-width: 640px) {
  .visually-hidden-desktop {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    outline: none;
  }
}
*,
::before,
::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  outline: none;
}
a:visited {
  color: inherit;
}
:is(.navigation, .user-actions, .user-nav, .modal, .auth) a {
  color: inherit;
}
:is(.navigation, .user-actions, .user-nav, .modal, .auth) a:focus {
  box-shadow: 0 0 0 1px #ffffff, 0 0 2px 3px #a6e5ff, 0 0 2px 3px #006aff;
}
:is(.navigation, .user-actions, .user-nav, .modal, .auth) a:hover {
  color: var(--dark-blue);
}
:is(.navigation, .modal) .button:focus {
  box-shadow: 0 0 0 1px #ffffff, 0 0 2px 3px #a6e5ff, 0 0 2px 3px #006aff;
}
:is(.main-nav, .user-actions, .user-nav) svg {
  fill: var(--blue);
}
.page,
.page__body {
  height: 100%;
}
.page__body {
  margin: 0;
  padding: 0;
  width: auto;
  font-size: 16px;
  font-family: 'Roboto', sans-serif, 'Arial';
  font-weight: 400;
  color: var(--basic-black);
  background-color: var(--white);
}
@media (min-width: 768px) {
  .page__body {
    font-size: 15px;
  }
}
img {
  width: 100%;
  height: auto;
}
.wrapper-content {
  padding: 0 15px;
}
@media (min-width: 768px) {
  .wrapper-content {
    padding: 0 40px;
  }
}
@media (min-width: 1240px) {
  .wrapper-content {
    padding: 0;
    width: 1200px;
    margin: 0 auto;
  }
}
@media (min-width: 1240px) {
  .wrapper {
    width: 1200px;
    margin: 0 auto;
  }
}
.container {
  background-color: #f6f6f6;
}
@media (min-width: 768px) {
  .container {
    background-color: #ffffff;
  }
}
:root {
  --blue: #006aff;
  --bright-blue: #a6e5ff;
  --dark-blue: #00008b;
  --white: #ffffff;
  --basic-black: #333333;
  --light-black: rgba(0, 0, 0, 0.1);
  --dark-gray: #666666;
  --gray: #b3b3b3;
  --light-gray: #e5e5e5;
  --extra-light-gray: #f6f6f6;
  --basic-green: #20a052;
  --light-green: #def6e7;
  --light: #fbfbfb;
  --light-blue: #f2faff;
}
.button {
  padding: 0;
  margin: 0;
  border: none;
  font-family: inherit;
  font-size: inherit;
  line-height: 20px;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}
.button:focus {
  box-shadow: 0 0 0 1px #ffffff, 0 0 2px 3px #a6e5ff, 0 0 2px 3px #006aff;
}
.button-main {
  padding: 5px 10px;
  color: #333333;
  border-radius: 5px;
  border: 1px solid var(--blue);
}
.button-main:hover {
  color: #ffffff;
  background-color: #006aff;
}
.layout {
  display: grid;
  grid-template-rows: max-content 1fr max-content;
  row-gap: 20px;
}
.layout__navigation {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (min-width: 768px) {
  .layout__navigation {
    column-gap: 10px;
  }
}
.layout__page-main {
  display: grid;
  grid-template-rows: max-content 1fr;
  row-gap: 30px;
}
.caption {
  margin: 0;
  font-weight: 400;
  padding: 0;
}
.caption--h1 {
  font-size: 1.375rem;
  line-height: 1.18em;
}
.caption--h2 {
  font-size: 1.125rem;
  line-height: 1.3rem;
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}
input[type='checkbox'],
input[type='radio'] {
  margin: 0;
  padding: 0;
}
.input {
  -webkit-appearance: none;
  outline: none;
}
.input:checked + label .pseudo-input {
  font-weight: 700;
  background-color: var(--light-blue);
  border-color: var(--blue);
  color: var(--blue);
}
.input:hover {
  border: 1px solid var(--blue);
  color: var(--dark-blue);
}
.input:focus + label .pseudo-input {
  box-shadow: 0 0 0 1px #ffffff, 0 0 2px 3px #a6e5ff, 0 0 2px 3px #006aff;
}
.input:focus {
  box-shadow: 0 0 0 1px #ffffff, 0 0 2px 3px #a6e5ff, 0 0 2px 3px #006aff;
}
.pseudo-input {
  display: block;
  width: 100%;
  padding: 13px 15px;
  line-height: 20px;
  font-size: inherit;
  font-family: inherit;
  border: 1px solid var(--light-gray);
  border-radius: 5px;
  cursor: pointer;
}
.pseudo-input:hover {
  color: var(--dark-blue);
  border-color: var(--blue);
}
@media (min-width: 768px) {
  .pseudo-input {
    padding: 5px 10px;
    border: none;
    border-radius: 0;
  }
}
.pseudo-checkbox {
  display: block;
  padding: 10px 18px;
  border: 1px solid var(--gray);
  border-radius: 5px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .pseudo-checkbox {
    padding: 5px 12px;
  }
}
.input--field {
  display: block;
  width: 100%;
  padding: 13px 15px;
  line-height: 20px;
  font-size: inherit;
  font-family: inherit;
  border: 1px solid var(--light-gray);
  border-radius: 5px;
  outline: none;
}
@media (min-width: 768px) {
  .input-number--sq {
    width: 60px;
  }
}
.skip-links {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 15px;
  background: #f498ad;
  transform: translateY(-100%);
}
.skip-links:focus {
  transform: translateY(0);
  top: 50px;
}
.page-header {
  padding-top: 10px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--light-gray);
}
@media (min-width: 1240px) {
  .page-header {
    padding: 3px 0 0 0;
  }
}
.page-header--layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 10px;
  row-gap: 10px;
  align-items: center;
}
.logo {
  display: flex;
  align-items: center;
  width: 75px;
  height: 40px;
  margin-left: 10px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .logo {
    margin-left: 0;
    padding: 0;
  }
}
@media (min-width: 1240px) {
  .logo {
    width: 112px;
    height: 42px;
  }
  .logo svg {
    width: 112px;
    height: 42px;
  }
}
.logo__icon {
  position: relative;
}
@media (min-width: 1240px) {
  .logo__icon {
    top: -3px;
  }
}
@media (max-width: 1239px) {
  .toggler[aria-expanded='false'] ~ .main-nav {
    display: none;
  }
}
.main-nav {
  position: absolute;
  top: 63px;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: var(--white);
}
@media (min-width: 768px) {
  .main-nav {
    padding: 0 40px;
  }
}
@media (min-width: 1240px) {
  .main-nav {
    position: static;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: transparent;
  }
}
.main-nav__item {
  display: grid;
  grid-template-columns: 1fr max-content;
}
.main-nav__item-link {
  display: block;
  padding: 12px 0 13px 15px;
  line-height: 28px;
  border-bottom: 1px solid var(--light-gray);
}
@media (min-width: 768px) {
  .main-nav__item-link {
    padding: 12px 0 13px 0;
  }
}
@media (min-width: 1240px) {
  .main-nav__item-link {
    position: relative;
    padding: 18px 10px 20px 10px;
    line-height: 20px;
    border: none;
  }
  .main-nav__item-link:hover::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: var(--blue);
  }
}
.main-nav__item-link span {
  display: block;
  border-right: 1px solid var(--gray);
}
@media (min-width: 1240px) {
  .main-nav__item-link span {
    border: none;
  }
}
.main-nav__toggler {
  padding: 0 25px;
  border-bottom: 1px solid var(--light-gray);
}
@media (min-width: 1240px) {
  .main-nav__toggler {
    display: none;
  }
}
@media (max-width: 1239px) {
  .main-nav__toggler[aria-expanded='false'] + .main-nav__submenu {
    display: none;
  }
}
.main-nav__item-icon {
  position: relative;
  top: 2px;
}
@media (min-width: 1240px) {
  .main-nav__submenu--layout {
    display: grid;
    grid-template-columns: max-content max-content max-content;
    grid-template-rows: max-content;
    column-gap: 60px;
    row-gap: 10px;
  }
}
.main-nav__submenu {
  grid-column: 1/-1;
  padding: 10px 30px;
  background-color: var(--light);
  border-bottom: 1px solid var(--light-gray);
}
@media (min-width: 1240px) {
  .main-nav__submenu {
    position: absolute;
    top: 62px;
    left: 50%;
    visibility: hidden;
    opacity: 0;
    transition-delay: 0.6s;
    transform: translateX(-50%);
    z-index: 1;
    width: 1200px;
    height: 400px;
    margin-top: 1px;
    padding: 40px 100px 40px 250px;
    background-color: var(--white);
    box-shadow: 0 5px 10px 0 var(--light-black);
  }
}
@media (min-width: 1240px) {
  .main-nav__item:hover .main-nav__submenu {
    opacity: 1;
    transition-property: opacity, visibility;
    transition-duration: 0.1s, 0.1s;
    transition-timing-function: linear, linear;
    transition-delay: 0.4s;
    visibility: visible;
  }
}
.main-nav__submenu-title {
  display: block;
  padding: 5px 0;
  font-size: 16px;
  font-weight: 700;
  margin-top: 15px;
}
@media (min-width: 1240px) {
  .main-nav__submenu-title {
    position: relative;
    margin: 0;
  }
  .main-nav__submenu-title:not(:first-child)::after {
    position: absolute;
    content: '';
    top: 50px;
    left: -31px;
    width: 2px;
    height: 200px;
    background-color: var(--extra-light-gray);
  }
}
@media (min-width: 1240px) {
  .main-nav__submenu-list {
    position: relative;
    grid-row: 2/3;
  }
}
.main-nav__submenu-list li a {
  display: block;
  padding: 10px 0;
  font-size: 14px;
}
@media (min-width: 1240px) {
  .main-nav__submenu-list li a {
    position: relative;
    padding: 8px 10px;
    font-size: inherit;
  }
}
@media (min-width: 1240px) {
  .main-nav__submenu-list li a span {
    position: relative;
  }
  .main-nav__submenu-list li a span:hover::after {
    position: absolute;
    content: '';
    left: 0;
    bottom: -4px;
    width: 100%;
    height: 1px;
    background-color: var(--dark-blue);
  }
}
.toggler {
  position: relative;
  width: 44px;
  height: 44px;
  margin-left: -10px;
}
@media (min-width: 1240px) {
  .toggler {
    display: none;
  }
}
.toggler[aria-expanded='false']::after {
  content: '';
  position: absolute;
  top: 16px;
  left: 10px;
  width: 23px;
  height: 2px;
  background-color: var(--basic-black);
  box-shadow: 0 6px 0 0 var(--basic-black), 0 12px 0 0 var(--basic-black);
}
.toggler[aria-expanded='true']::before,
.toggler[aria-expanded='true']::after {
  content: '';
  position: absolute;
  top: 21px;
  left: 10px;
  width: 23px;
  height: 2px;
  background-color: var(--basic-black);
}
.toggler[aria-expanded='true']::before {
  transform: rotate(-45deg);
}
.toggler[aria-expanded='true']::after {
  transform: rotate(45deg);
}
.label-popup,
.label-popup--add {
  display: none;
}
.user-actions__like:hover .label-popup,
.user-nav__button-profile:hover .label-popup {
  display: block;
  position: absolute;
  bottom: -45px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
  font-size: 14px;
  padding: 5px;
  color: var(--white);
  background-color: var(--gray);
  border-radius: 5px;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .user-actions__add:hover .label-popup--add {
    display: block;
    position: absolute;
    bottom: -45px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    font-size: 14px;
    padding: 5px;
    color: var(--white);
    background-color: var(--gray);
    border-radius: 5px;
    white-space: nowrap;
  }
}
.user-actions ul {
  display: flex;
  column-gap: 10px;
  align-items: center;
}
@media (min-width: 641px) {
  .user-actions ul {
    column-gap: 10px;
  }
}
.user-actions__like,
.user-actions__add {
  position: relative;
  display: block;
  padding: 13px;
  border-radius: 50%;
}
.user-actions__like:hover,
.user-actions__add:hover {
  outline: 2px solid var(--blue);
  background-color: var(--light-blue);
}
.user-actions__like:active,
.user-actions__add:active {
  background-color: var(--blue);
  color: var(--white) !important;
}
.user-actions__like:active svg,
.user-actions__add:active svg {
  fill: var(--white);
}
@media (min-width: 641px) {
  .user-actions__like,
  .user-actions__add {
    padding: 11px;
    background-color: var(--extra-light-gray);
  }
}
@media (max-width: 339px) {
  .user-actions__like {
    display: none;
  }
}
@media (min-width: 641px) {
  .user-actions__like {
    border-radius: 50%;
  }
}
@media (min-width: 641px) {
  .user-actions__add {
    display: flex;
    column-gap: 2px;
    border-radius: 5px;
  }
}
.user-actions__icon {
  position: relative;
  top: 1px;
  display: block;
}
@media (min-width: 768px) {
  .user-actions__icon {
    top: 0;
  }
}
.user-actions__icon-heart {
  top: 2px;
}
@media (min-width: 768px) {
  .user-actions__icon-heart {
    top: 1;
  }
}
.user-nav__item--disabled {
  display: none;
}
.user-nav__button-login {
  padding: 13px 12px 12px 12px;
  border-radius: 5px;
  background-color: var(--extra-light-gray);
}
.user-nav__button-login:hover {
  outline: 2px solid var(--blue);
  background-color: var(--light-blue);
}
.user-nav__button-login:active {
  background-color: var(--blue);
  color: var(--white) !important;
}
@media (min-width: 641px) {
  .user-nav__button-login {
    padding: 11px;
  }
}
.user-nav__button-profile {
  position: relative;
  display: block;
  width: 44px;
  height: 44px;
  border-radius: 50px;
  background-color: var(--extra-light-gray);
}
.user-nav__button-profile:hover {
  outline: 2px solid var(--blue);
  background-color: var(--light-blue);
}
.user-nav__button-profile:active {
  background-color: var(--blue);
}
.user-nav__button-profile:active svg {
  fill: var(--white);
}
.user-nav__icon {
  display: block;
  position: relative;
  text-align: center;
  top: 8px;
  left: 5px;
  width: 30px;
  height: 30px;
}
.user-nav__item-list {
  position: absolute;
  top: 59px;
  right: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 10px;
  background-color: var(--light-blue);
  border-radius: 5px;
}
.user-nav__item-list a {
  display: block;
  padding: 11px;
}
.user-nav__item-list--disabled {
  display: none;
}
@media (min-width: 1240px) {
  .user-nav__item-list {
    right: 110px;
    width: 250px;
    height: max-content;
    padding: 10px;
    box-shadow: 0 5px 10px 0 var(--light-black);
  }
}
@media (min-width: 768px) {
  .auth {
    width: 50%;
  }
}
.auth__form {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
.auth__form label:hover {
  color: var(--dark-blue);
}
.auth__form fieldset {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  border: 1px solid var(--light-gray);
  border-radius: 5px;
}
.auth__form fieldset:hover {
  border-color: var(--blue);
}
.auth__form fieldset legend {
  margin-bottom: 10px;
}
.auth__form fieldset div {
  flex-direction: row;
  column-gap: 15px;
}
.auth__form fieldset input {
  margin-right: 10px;
}
.auth__form > div {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 7px;
}
.auth__form > div:nth-child(3) {
  flex-direction: row;
  column-gap: 15px;
}
.auth__form > div:nth-child(5) {
  flex-direction: row;
  justify-content: space-between;
  color: var(--blue);
  text-decoration: underline;
  margin-left: -10px;
  margin-right: -10px;
}
.auth__form > div:nth-child(5) a {
  display: block;
  padding: 10px;
  text-decoration: 1px solid var(--blue);
}
.auth__form > div:nth-child(4) {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: baseline;
  column-gap: 10px;
}
.auth__form > div:nth-child(4) input {
  position: relative;
  top: 1px;
}
.auth__form button {
  background-color: var(--blue);
  color: var(--white);
  padding: 14px;
  border-radius: 5px;
}
.auth__form button:hover {
  padding-top: 13px;
  padding-bottom: 13px;
  background-color: #004099;
  border: 1px solid var(--blue);
  box-sizing: border-box;
}
.auth__form button:active {
  background-color: #002a66;
}
.auth--restore p {
  margin: 0 0 20px 0;
}
.modal {
  display: none;
  position: fixed;
  top: 50vh;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  margin: 0 auto;
  width: 90%;
  padding: 40px 20px 20px 20px;
  border-top: 2px solid var(--blue);
  background-color: var(--light-blue);
  box-shadow: 0 5px 10px 0 var(--light-black);
}
@media (min-width: 768px) {
  .modal {
    width: 370px;
    top: 40%;
  }
}
.modal--reg {
  padding-bottom: 40px;
}
.modal h2 {
  margin-bottom: 30px;
}
.modal--show {
  display: block;
}
.modal__button-close {
  position: absolute;
