.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  outline: none;
}

.visually-hidden-mobile {
  @media (min-width: @tablet-width) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    outline: none;
  }
}

.visually-hidden-desktop {
  @media (max-width: 640px) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    outline: none;
  }
}

*,
::before,
::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  outline: none;

  &:visited {
    color: inherit;
  }
}

:is(.navigation, .user-actions, .user-nav, .modal, .auth) a {
  color: inherit;

  &:focus {
    box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 2px 3px rgb(166, 229, 255), 0 0 2px 3px rgb(0, 106, 255);
  }

  &:hover {
    color: var(--dark-blue);
  }
}

:is(.navigation, .modal) .button {
  &:focus {
    box-shadow: 0 0 0 1px rgb(255, 255, 255), 0 0 2px 3px rgb(166, 229, 255), 0 0 2px 3px rgb(0, 106, 255);
  }
}

:is(.main-nav, .user-actions, .user-nav) svg {
  fill: var(--blue);
}
