.button-filter {
  position: relative;
  border-radius: 5px;
  padding: 13px 10px;
  border: 1px solid @light-gray;

  &:active,
  &:hover {
    border-color: var(--blue);
  }

  &--deal,
  &--price,
  &--add {
    display: none;

    @media (min-width: @tablet-width) {
      display: block;
    }
  }
  @media (min-width: @tablet-width) {
    padding: 7px 25px 7px 10px;
    // text-align: start;
    border: 1px solid @light-gray;
  }
}

.button-filter__icon {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  fill: var(--blue);

  @media (min-width: @tablet-width) {
    right: 5px;
  }
}

.button-filter__icon--rotate {
  transform: rotate(180deg) translateY(50%);
}

.button-filter--type {
  display: block;
  width: 100%;
  border: 1px solid @light-gray;
  text-align: start;
  background-color: @white;
}
