.page-header {
  padding-top: 10px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--light-gray);

  @media (min-width: @desktop-width) {
    padding: 3px 0 0 0;
  }
}

.page-header--layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 10px;
  row-gap: 10px;
  align-items: center;
}
